export const getUserById = (id) => {
  const response = jQuery.ajax({
    method: 'GET',
    url: `http://localhost:4444/users/${id}`,
  }).then(function(data) {
    console.log('data>>>>', data)
    const keys = [
      'zipcode',
      'first_name',
      'last_name',
      'address',
      'number',
      'floor',
      'locality',
      'city',
      'state',
      'country',
      'phone',
      'between_streets',
      'reference'
    ]
    const addresses = response.address
    let address = {}
    let user = {
      contact_name: response.name,
      contact_phone: response.phone,
      // contact_accepts_marketing: true,
    }
    addresses.map(item => {
      keys.map((key) => {
        address = {
          ...address, [`${item.type}_${key}`]: `${item[key]}` || null
        }
        return true
      })    
    })
    console.log('returned===>', { user, ...address })
    return  { user, ...address }
  });
  console.log('await finish')
}